.foodie-container {
  display: block;
  width: 100%;
  max-width: 600px;

  margin: 0 auto;
  padding: 2em 0 0;
}

.header-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;

  width: 100%;
  max-width: 350px;

  margin: 0 auto;
  padding: 0.5em 0;
}

.header-container div{
  display: block;
  width: 100px;
  height: 80px;
}

.header-container svg,
.header-container svg path {
  display: block;
  width: 100px;
  height: 80px;
}

.header-container h1 {
  display: block;
  /* width: 100%; */

  padding: 0.5em 0;

  color: #fff;
  font-size: 2.4em;
  font-weight: 700;
}

#plcInput {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 1em auto;
  border: none;
  border-radius: 4px;

  font-size: 1em;
  font-weight: 700;
  text-align: center;

  padding: 0.5em 0;
}

button {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0.5em auto;
  padding: 0.5em 0;

  border-radius: 4px;
  border: none;

  font-size: 1em;
  font-weight: 700;

  color: #fff;

  text-transform: uppercase;
}

.add-btn {
  background: #05dfd7;
}

.draw-btn {
  background: #52de97;
}

.restart-btn {
  background: #fe346e;
}

.plc-container {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0 auto;
}

.plc-container .item-box {
  display: block;

  width: 100%;

  /* font-size: 1.4em; */
  /* color: #fff; */
  
  /* padding: 0.5em 0; */
  margin: 0.3em auto;

  background: rgba(0, 0, 0, 0.24);
  border-radius: 4px;

  cursor: pointer;

  position: relative;
}

.item-box p {
  font-size: 1.4em;
  color: #fff;

  padding: 0.6em 0;
}

.item-box .close-icon{
  display: block;
  width: 18px;
  height: 18px;

  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  z-index: 1;
}

.item-box .close-icon svg,
.item-box .close-icon svg path{
  display: block;
  width: 18px;
  height: 18px;
  /* fill: rgba(255, 255, 255, 0.7); */
  fill: #fe346e;
}

h2,
h3 {
  display: block;
  width: 100%;

  max-width: 350px;
  margin: 0 auto;

  color: #fff;
}

h2 {
  font-size: 2.4em;
  background: #4f3961;
  padding: 0.4em 0;
  border-radius: 4px;
}

h3 {
  font-size: 1.3em;
  padding: 0.8em 0;
}